<script>
export default
{
	name: 'Subjects',
	props: ['questionData', 'subject', 'selectedQuestion', 'showedQuestionsList'],
	computed:
	{
		isSubjSelected()
		{
			return this.selectedQuestion.subject==this.subject
		},
		isSubjectEnded()
		{
			return this.checkSubject()
		}
	},
	methods:
	{
		checkSubject()
		{
			let checkedPointsCount = 0;
			for ( let point of this.questionData.questions)
			{
				if (point.selected) checkedPointsCount++;
			}

			if (checkedPointsCount === this.questionData.questions.length)
				return true
			else
				return false;
		}
	}
}
</script>

